@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
}
*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  background-color: #F4F4F4;
  font-family: "Roboto", sans-serif;
}

.container {
  margin: 0 auto;
  padding: 60px 0px;
  max-width: 1170px;
}
@media (max-width: 1200px) {
  .container {
    max-width: 830px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 600px;
  }
}

.row {
  display: flex;
  justify-content: space-between;
}

.card-holder + .sidebar {
  margin-left: 30px;
}

.toggle-sidebar {
  display: none;
  position: fixed;
  z-index: 9;
  right: 15px;
  top: 8px;
  width: 45px;
  height: 45px;
  background-color: rgb(71, 71, 71);
  justify-content: center;
  align-items: center;
}
@media (max-width: 829px) {
  .toggle-sidebar {
    display: flex;
  }
}

.menu-icon-wrapper {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  border: none;
  background-color: transparent;
}

.menu-icon {
  position: relative;
  width: 30px;
  height: 5px;
  background-color: #fff;
}

.menu-icon::before {
  position: absolute;
  left: 0;
  top: -10px;
  content: "";
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.menu-icon::after {
  position: absolute;
  left: 0;
  top: 10px;
  content: "";
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.menu-icon.menu-icon-active {
  background-color: transparent;
}

.menu-icon.menu-icon-active::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.menu-icon.menu-icon-active::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.sidebar {
  min-width: 200px;
  width: 270px;
}
@media (max-width: 829px) {
  .sidebar {
    margin-left: 0 !important;
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    bottom: 0;
    padding: 15px;
    box-shadow: 0px 0px 20px 4px rgba(120, 116, 116, 0.35);
    overflow-y: scroll;
    transform: translateX(-150%);
    transition: transform 0.3s ease-in;
  }
}
.sidebar--mobile-active {
  transform: translateX(0%);
}

.widget {
  position: relative;
}
.widget__body--hidden {
  display: none;
}
.widget--top-30 {
  margin-top: 30px !important;
}
.widget__hidden {
  display: none;
}
.widget + .widget {
  margin-top: 33px;
}
.widget__title {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.16;
  color: #000000;
}
.widget__title::after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 0;
  vertical-align: middle;
  background-image: url("../../img/icons/chevron.svg");
  background-repeat: no-repeat;
  background-size: initial;
  background-position: center;
}
.widget__title--active::after {
  transform: rotate(180deg);
}
.widget__btn-show-hidden {
  display: inline-block;
  margin-top: 15px;
  padding: 0;
  background: transparent;
  border: none;
  font-family: inherit;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.19;
  color: #4290c9;
  cursor: pointer;
  transition: color 0.2s ease-in;
}
.widget__btn-show-hidden:active, .widget__btn-show-hidden:focus {
  outline: none;
}
.widget__btn-show-hidden:hover {
  color: #007fda;
  text-decoration: underline;
}

.location__row {
  display: flex;
}
.location__row:first-child {
  overflow: hidden;
  border-radius: 4px 4px 0 0;
  border: 1px solid #D6D6D6;
}
.location__row:last-child {
  overflow: hidden;
  border-radius: 0 0 4px 4px;
  border: 1px solid #D6D6D6;
  border-top: none;
}
.location__btn {
  flex-grow: 1;
  font-family: inherit;
  height: 48px;
  padding: 0 5px;
  background-color: transparent;
  border: none;
  text-align: center;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 50px;
  color: #3c3c3c;
  cursor: pointer;
  transition: background-color 0.2s ease-in;
}
.location__btn:hover {
  background-color: #fff0bb;
}
.location__checkbox {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.location__checkbox:checked + .location__btn {
  background-color: #FFCF26;
}
.location__checkbox:checked + .location__btn:hover {
  background-color: #f2bc00;
}

.radio {
  display: block;
  cursor: pointer;
}
.radio + .radio {
  margin-top: 15px;
}
.radio__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.radio__fake {
  position: relative;
  display: inline-block;
  margin-right: 7px;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #777777;
  border-radius: 50%;
  vertical-align: bottom;
}
.radio__real:checked + .radio__fake {
  background-color: #ffe074;
}
.radio__real:checked + .radio__fake::before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background: #3c3c3c;
  transform: translate(-50%, -50%);
}
.radio__real:checked ~ .radio__title {
  font-weight: 500;
}
.radio__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}

.checkbox {
  display: block;
  cursor: pointer;
}
.checkbox + .checkbox {
  margin-top: 15px;
}
.checkbox--hidden {
  display: none;
}
.checkbox__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.checkbox__fake {
  position: relative;
  display: inline-block;
  margin-right: 7px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: 1px solid #777777;
  border-radius: 2px;
  vertical-align: bottom;
}
.checkbox__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}
.checkbox__real:checked + .checkbox__fake {
  background-color: #ffe074;
}
.checkbox__real:checked + .checkbox__fake::before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  width: 18px;
  height: 18px;
  background-image: url("../../img/icons/check.svg");
  background-size: initial;
  background-position: center;
  transform: translate(-50%, -50%);
}
.checkbox__real:checked ~ .checkbox__title {
  font-weight: 500;
}

.toggle {
  cursor: pointer;
}
.toggle__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.toggle__fake {
  position: relative;
  display: inline-block;
  margin-right: 7px;
  width: 37px;
  height: 20px;
  background: #c4c4c4;
  border-radius: 10px;
  vertical-align: bottom;
}
.toggle__fake:before {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #3c3c3c;
  border-radius: 50%;
  transition: all 0.2s ease-in;
}
.toggle__real:checked + .toggle__fake {
  background: #FFCF26;
}
.toggle__real:checked + .toggle__fake::before {
  left: 19px;
}
.toggle__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}
.toggle__real:checked ~ .toggle__title {
  font-weight: 500;
}

.form-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
  border: 1px solid #D6D6D6;
  border-radius: 4px;
  background-color: transparent;
  font-family: inherit;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1;
  color: #3c3c3c;
  cursor: pointer;
  transition: background-color 0.2s ease-in;
}
.form-btn + .form-btn {
  margin-top: 10px;
}
.form-btn img {
  margin-right: 6px;
}
.form-btn--submit {
  background: #FFCF26;
  border: none;
  color: #000;
}
.form-btn--submit:hover {
  background: #ffe173;
}

.card-holder {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 599px) {
  .card-holder {
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: center;
  }
}

.card-holder .card-link {
  margin-bottom: 30px;
}

.card-link {
  display: inline-block;
  text-decoration: none;
}
.card-link--hidden {
  display: none !important;
}
.card-link:hover {
  opacity: 0.9;
  transform: translateY(-3px);
  transition: 0.2;
}

.card {
  position: relative;
  width: 270px;
  background-color: #FFFFFF;
}
.card__badges {
  position: absolute;
  top: 10px;
  left: 15px;
  right: 15px;
  display: flex;
}
.card__badges > * + * {
  margin-left: 10px;
}
.card__badge {
  display: inline-block;
  height: 22px;
  padding: 0 10px;
  background: grey;
  border-radius: 4px;
  font-size: 14px;
  line-height: 23px;
  color: #000000;
}
.card__badge--class {
  background: #FFCF26;
}
.card__badge--loan {
  background: #11BE75;
}
.card__img {
  display: block;
}
.card__content {
  padding: 16px 15px 20px;
}
.card__title {
  margin: 0;
  margin-bottom: 11px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.16;
  color: #000000;
}
.card__description {
  margin: 0;
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 1.14;
  color: #000000;
}
.card__metro {
  margin: 0;
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 1.14;
  color: #000000;
}
.card__metro::before {
  content: "";
  margin-right: 10px;
  display: inline-block;
  vertical-align: baseline;
  width: 16px;
  height: 11px;
  background-image: url("../../img/icons/metro.svg");
  background-repeat: no-repeat;
  background-size: initial;
}
.card__address {
  margin: 0;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 1.14;
  color: #777777;
}

.btn-more {
  width: 100%;
  height: 80px;
  border: none;
  background: #D6D6D6;
  border-radius: 4px;
  text-align: center;
  font-size: 18px;
  line-height: 21px;
  color: #4290c8;
  cursor: pointer;
}
.btn-more:hover {
  background-color: #bdbdbd;
  transition: all ease 0.2s;
}
.btn-more img {
  display: inline-block;
  vertical-align: bottom;
  margin-right: 10px;
}